import React, {Component} from 'react';

class Timer extends Component{
    state={ 

        date: new Date()
    }


    runIt(){
        setInterval(()=>{
           this.setState({ date: new Date()});
        }, 1000)
    }

    render() {
        return (
            <div className="App">
                <p>London Clock</p>

                <p> { this.state.date.toLocaleTimeString()} </p>

                {this.runIt()}
            </div>

        );
    }

}

export default Timer;